<template>
  <div>
   <login-top tip="登录supermall">
    <div slot="right" style="font-size:3.611vw;color:blue" @click="$router.push('/prifile')">去注册</div>
   </login-top>
   
   <login-text label="账号" placeholder='请输入账号' rule="^.{6,16}$"  @inputchange="res=>model.username=res" style="margin:5.556vw 0"></login-text>
   <login-text label="密码" placeholder='请输入密码' type="password" rule="^.{6,16}$"  @inputchange="res=>model.password=res"></login-text>
   <login-btn btntext="登录" @registersubmit="AjaxInsert"></login-btn>
  </div>
</template>

<script>
import LoginTop from '../components/common/LoginTop'
import LoginText from '../components/common/LoginText'
import LoginBtn from '../components/common/LoginBtn'
export default {
  data(){
    return{
      model:{
username:'',
password:''}

    }
  },
components:{
  LoginTop,
  LoginText,
  LoginBtn
},
methods:{
  async AjaxInsert(){
    if(this.model.username&&this.model.password){
      const res = await this.$http.post('/login',this.model)
      this.$msg.fail(res.data.msg)
      if(res.data.code == 301 || res.data.code == 302){
        return
      }
      localStorage.setItem('token',res.data.token)
      localStorage.setItem('id',res.data.id)
      setTimeout(()=>{
this.$router.push('/category')
      },1000)
    }
    else{
      this.$msg.fail('格式不正确，请重新输入！')
    }
  }
  //------------------------------------------------------------------
//  async registersubmit(){
//    let rulg = /^.{6,16}$/
//   if( rulg.test(this.model.username) && rulg.test(this.model.password)){
//   const res=await this.$http.post('/register',this.model)
//   this.$msg.fail(res.data.msg)
//   }else{
//     this.$msg.fail('格式不正确,重新输入')
//   }
// },
//--------------------------------------------------------
// inputchange(content){
  
//   this.name=content
// }
},
mounted(){
 document.querySelector('body').setAttribute('style', 'background-color:#f7f7f7')
},
}

</script>

<style scoped>
body{
  background: cornsilk;
  width: 100%;
  height: 100%;
}
</style>